let THREE = window.THREE

import LMCamera from './LMCamera'
import LMRender from './LMRender'
import LMAudio from './LMAudio'


let that = null

function handleWindowResize () {
  let height = window.innerHeight
  let width = window.innerWidth
  that.lmrender.render.setSize(width, height)
  that.lmcamera.camera.aspect = width / height
  that.lmcamera.camera.updateProjectionMatrix()
}
function handleMouseMove (event) {
}
function handleMouseDown (event) {
}
function handleMouseUp (event) {
}
function handleTouchStart (event) {
}
function handleTouchEnd (event) {
}

function handleTouchMove (event) {
}

/**
 * threejs 大多数应用都需要的东西,如场景相机等.
 */
class LMThree {
  constructor({canvasHeight, canvasWidth, canvasFatherId}) {
    that = this
    window.THREE = THREE
    // 场景
    this.scene = null
    // 相机
    this.lmcamera = null
    // 渲染器
    this.lmrender = null
    // 声音
    this.lmaudio = null

    this.initScene()
    this.initCamera()
    this.initRender({canvasHeight, canvasWidth, canvasFatherId})
    this.initAudio()
    this.controls = new THREE.TrackballControls(this.lmcamera.camera)
  }

  initScene() {
    this.scene = new THREE.Scene()
    this.scene.background = new THREE.Color( 0x0a0026 );
  }

  initCamera() {
    this.lmcamera = new LMCamera()
  }

  initRender({canvasHeight, canvasWidth, canvasFatherId}) {
    this.lmrender = new LMRender({canvasHeight, canvasWidth, canvasFatherId})
  }

  initAudio() {
    this.lmaudio = new LMAudio()
    this.scene.add(this.lmaudio.sound)
  }

  initBroserInterface() {
    window.addEventListener('resize', handleWindowResize, false)
    document.addEventListener('mousemove', handleMouseMove, false)
    document.addEventListener('mousedown', handleMouseDown, false)
    document.addEventListener('mouseup', handleMouseUp, false)
    document.addEventListener('touchstart', handleTouchStart, false)
    document.addEventListener('touchend', handleTouchEnd, false)
    document.addEventListener('touchmove', handleTouchMove, false)
  }
}
export default LMThree



