
let defaultColors = [
  { pct: 0, color: { r: 0xf4, g: 0xee, b: 0x42 } },
  { pct: 0.5, color: { r: 0x41, g: 0xf4, b: 0x68 } },
  { pct: 1, color: { r: 0x41, g: 0xdf, b: 0xf4 } }
];

class AudioBars {
  constructor (barNumber, containerClass) {
    // 创建频谱元素
    let barContainer = document.getElementsByClassName(containerClass)[0]
    this.Bars = [];
    let width = ( ( 1 / barNumber ) * 100 ) + '%';
    for ( let i = 0; i < barNumber; i++ ) {
      let bar = document.createElement('div');
      bar.classList.add('bar');
      bar.style.width = width;
      barContainer.appendChild(bar);
			this.Bars.push(bar);
    }
    // 设置颜色
    this.colorRange = false || defaultColors;
  }

  update (spectrum) {
    let i = 0;
    spectrum.forEach( ( sound ) => {
			let height = 0.8 * sound;
      this.Bars[i].style.height = height + 'px';
      this.Bars[i].style.background = this.color( sound / 255 );
      i++;
    });
  }

  //Determine color based on percentage ( CurrentVolume/MaxVolume )
  color( pct ) {
    for (var i = 1; i < this.colorRange.length - 1; i++) {
        if (pct < this.colorRange[i].pct) {
            break;
        }
    }
    let lower = this.colorRange[i - 1];
    let upper = this.colorRange[i];
    let range = upper.pct - lower.pct;
    let rangePct = (pct - lower.pct) / range;
    let pctLower = 1 - rangePct;
    let pctUpper = rangePct;
    let color = {
        r: Math.floor(lower.color.r * pctLower + upper.color.r * pctUpper),
        g: Math.floor(lower.color.g * pctLower + upper.color.g * pctUpper),
        b: Math.floor(lower.color.b * pctLower + upper.color.b * pctUpper)
    };
    return '#' + this.componentToHex(color.r) + this.componentToHex(color.g) + this.componentToHex(color.b);
  }

  componentToHex(c) {
    var hex = c.toString(16);
    return hex.length == 1 ? "0" + hex : hex;
  }
}
export default AudioBars


