<template>
 <div class="box">
  <div id="canvasFather"></div>
  <div class="bar-container"></div>
  <div class="playState">{{playState}}</div>
  <button @click="play" :class={active:isReadPlay}>播放</button>
  <div>{{process}}</div>
 </div>
</template>

<script>
import LMThree from './THREEJS/LMThree'
import AudioBars from './AudioBars'
import axios from 'axios'
let lmthree = null
let audioBars = null
let that = null

export default {
  data() {
    return {
      process: 0,
      playState: '未播放',
      isReadPlay: false
    }
  },
  mounted() {
    that = this
    // threejs
    lmthree = new LMThree({canvasHeight: 500, canvasWidth: 800, canvasFatherId: 'canvasFather'})
    let barsLength = lmthree.lmaudio.analyser.getFrequencyData().length;
    audioBars = new AudioBars(barsLength, 'bar-container',)
    this.loop()
    window.lmthree = lmthree
    this.loadFile('https://1097364388.github.io/static/music/tianfen.mp3')
  },
  methods: {
    // 开始动画
    loop() {
      this.isReadPlay = lmthree.lmaudio.isReadPlay
      this.process = lmthree.lmaudio.process
      that.playState = lmthree.lmaudio.playing === false ? '未播放' :'播放中'
      lmthree.controls.update();
      lmthree.lmrender.render.render(lmthree.scene, lmthree.lmcamera.camera);
      if (lmthree.lmaudio.playing) {
        let data = lmthree.lmaudio.analyser.getFrequencyData()
        audioBars.update(data)
      }
      requestAnimationFrame(that.loop);
    },
    loadFile(fileURL) {
      lmthree.lmaudio.loadFile(fileURL)
    },
    play() {
      if (this.isReadPlay === false) {
        alert('没有加载完...')
        return
      }
      confirm("确定播放?")
      lmthree.lmaudio.sound.pause();
      lmthree.lmaudio.sound.play();
      lmthree.lmaudio.playing = true;
    }
  }
}
</script>

<style lang="scss">
  #drag {
    width: 100%;
    height: 100%;
  }
  .box {
    width: 100%;
    height: 100%;
  }
  #canvasFather{
    margin: auto;
  }
  canvas {
    border: 1px solid #ccc;
  }
  .active {
    background-color: #007acc;
  }
  .bar-container {
    position: fixed;
    bottom: 436px;
    width: 800px;
    transform: rotate(180deg);
    .bar {
      float: right;
    }
  }
</style>



