let THREE = window.THREE

class LMAudio {
  constructor() {
    this.listener = new THREE.AudioListener();
    this.audioLoader = new THREE.AudioLoader(); // 加载器
    this.sound = new THREE.Audio(this.listener); // 声音
    this.analyser = new THREE.AudioAnalyser(this.sound, 512 ); // 分析器
    this.sound.setVolume(1)
    this.playing = false
    this.isReadPlay = false
    this.process = 0
  }
    
  loadFile(URL) {
    let that = this
    that.audioLoader.load(URL, (buffer) => {
      that.sound.setBuffer( buffer );
      that.sound.setLoop( true );
      that.sound.setVolume(1);
      that.isReadPlay = true
    },(xhr)=>{
       let perc = (xhr.loaded / xhr.total * 100)
       that.process = `加载: ${Math.floor(perc)}%`
    });
  }
  // 载入文件
  loadSong (file,process) {
    let that = this
    let songname = file.name;
    let url = window.URL.createObjectURL( file );
    this.audioLoader.load( url, ( buffer ) => {
      that.sound.setBuffer( buffer );
      that.sound.setLoop(true);
      that.sound.setVolume(1);
      if (that.playing) {
        that.sound.stop();
      }
      // that.sound.play();
      // that.playing = true;
    }, ( xhr ) => {
      let perc = (xhr.loaded / xhr.total * 100);
      let loaded = 'Loading ' + songname + ': ' + Math.floor(perc) + '%';
      process.data = loaded
      console.log( loaded )
    }, ( err ) => {
      console.log( err )
    });
  }
}
export default LMAudio



