let THREE = window.THREE

class LMRender {
  constructor({canvasHeight, canvasWidth, canvasFatherId}) {
    this.render = new THREE.WebGLRenderer({ alpha: true, antialias: true })
    this.render.setSize(canvasWidth, canvasHeight)
    this.render.shadowMapEnabled = true
    let container = document.getElementById(canvasFatherId)
    container.appendChild(this.render.domElement)
  }
}
export default LMRender



